import React, {Component} from 'react';
import '../../css/LoginDialog.css'
import {signUp, signIn, sendPasswordResetEmail} from './leanCloud'
import ForgotPasswordForm from './ForgotPasswordForm'
import SignUpForm from './SignUpForm'
import SignInForm from './SignInForm'

import $ from 'jquery';

window.jQuery = $;
require('semantic-ui/dist/semantic.min.css');
require('semantic-ui/dist/semantic.min.js');

export default class LoginDialog extends Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'signIn',//
            formData: {
                email: '',
                username: '',
                password: '',
            },
            status_code: {
                1: '',
                125: '',
                202: '!',
                203: '!',
                204: '',
                205: '',
                210: '!',
                211: '!',
                219: '!',
                unknown: '!'
            },
            error_record: ''
        }
    }

    // dom 
    componentDidMount() {
        this.check(".ui.large.form");
    }


    //
    componentWillUpdate() {
        this.check(".ui.large.form");
    }

    /**/
    signUp(e) {
        e.preventDefault();// 
        let {email, username, password} = this.state.formData;
        let success = (user) => {
            this.props.onSignUp.call(null, user)
        };
        let error = (error) => {
            console.log(error);
            this.getErrorMessage(error.code);
            $('.ui.basic.modal').modal('show');
        };
        if (!username || !password || !email) {
            return false;
        }
        if (!/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/.test(email)) {
            return false;
        }
        if (!/^\w{6,}$/.test(password)) {
            return false;
        }
        
        /*leanCloud.js*/
        signUp(email, username, password, success, error);
    }

    /**/
    signIn(e) {
        e.preventDefault();
        let {username, password} = this.state.formData;
        let success = (user) => {
            this.props.onSignIn.call(null, user);
        };
        let error = (error) => {
            console.log(error);
            this.getErrorMessage(error.code);
            $('.ui.basic.modal').modal('show');
        };
        if (!username || !password) {
            return false;
        }
        if (!/^\w{6,}$/.test(password)) {
            return false;
        }
        signIn(username, password, success, error);

    }

    //
    getErrorMessage(code) {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.error_record = this.state.status_code[code] || this.state.status_code.unknown;
        this.setState(stateCopy);
    }

    changeFormData(key, e) {
        let stateCopy = JSON.parse(JSON.stringify(this.state)); //  JSON 
        stateCopy.formData[key] = e.target.value;
        this.setState(stateCopy)
    }

    render() {
        let center = 'center';
        return (
            <div className="ui middle aligned center aligned grid">
                <div className="column">
                    <h2 className="ui teal image header">
                        <img src="http://ohggtqwxx.bkt.clouddn.com/todo.png" className="image" alt=""/>
                        <div className="content addFont">
                            A simple and pithy TodoList
                        </div>
                    </h2>
                    {this.state.selectedTab === 'signIn'
                        ? <SignInForm
                            formData={this.state.formData}
                            onSignIn={this.signIn.bind(this)}
                            onChange={this.changeFormData.bind(this)}
                            onForgotPassword={this.showForgotPassword.bind(this)}
                            returnToSignUp={this.returnToSignUp.bind(this)}/>
                        : this.state.selectedTab === 'signUp'
                            ? <SignUpForm
                                formData={this.state.formData}
                                onChange={this.changeFormData.bind(this)}
                                onSignUp={this.signUp.bind(this)}
                                returnToSignIn={this.returnToSignIn.bind(this)}
                            />
                            : <ForgotPasswordForm
                                formData={this.state.formData}
                                onSubmit={this.resetPassword.bind(this)}
                                onChange={this.changeFormData.bind(this)}
                                returnToSignIn={this.returnToSignIn.bind(this)}/>
                    }


                    <div className="ui basic modal">
                        <div className="ui icon header">
                            <i className="remove icon"></i> 
                        </div>
                        <div className="content">
                            <div className="description">
                                <p style={{textAlign: center}}>{this.state.error_record}</p>
                            </div>
                        </div>
                        <div className="actions">
                            <div className="ui green ok inverted button">
                                
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }

    //
    showForgotPassword() {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.selectedTab = 'forgotPassword';
        this.setState(stateCopy);
    }

    //
    returnToSignUp() {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.selectedTab = 'signUp';
        this.setState(stateCopy);
    }

    //
    returnToSignIn() {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.selectedTab = 'signIn';
        this.setState(stateCopy);
    }

    //
    resetPassword(e) {
        e.preventDefault();
        let success = (success) => {
            alert("!")
        };
        let error = (error) => {
            console.log(error);
            this.getErrorMessage(error.code);
            $('.ui.basic.modal').modal('show');
        };
        /**/
        if (!this.state.formData.email) {
            return false;
        }
        /**/
        if (!/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/.test(this.state.formData.email)) {
            return false;
        }
        sendPasswordResetEmail(this.state.formData.email, success, error);
    }

    //
    check(el) {
        $(el).form({
            fields: {
                email: {
                    identifier: 'email',
                    rules: [
                        {
                            type: 'email',
                            prompt: ''
                        }
                    ]
                },
                username: {
                    identifier: 'username',
                    rules: [
                        {
                            type: 'empty',
                            prompt: ''
                        }
                    ]
                },
                password: {
                    identifier: 'password',
                    rules: [
                        {
                            type: 'empty',
                            prompt: ''
                        },
                        {
                            type: 'minLength[6]',
                            prompt: ' {ruleValue} '
                        }
                    ]
                }
            },
            inline: true,
            on: 'blur'
        });
    }
}


